<template>
  <div class="shop">
      <header>
          <nav>
              <router-link to="/"><i class="fas fa-chevron-left"></i></router-link>
          </nav>
          <div class="header-wrap">
              <img src="" alt="">
              <div class="header-item">
                  <h2>苏丹皇宫是倒过来看</h2>
                  <div>
                      <span>4.4</span><span>月售45单</span>
                      <span>蜂鸟转送</span>
                  </div>
                  <p>欢迎光临，用餐高峰期请提前下单，谢谢。</p>
              </div>
                <div class="bottom-item-bottom" v-bind:class="{ 'bottom-item-bottom-click': isShow}">
                    <div class="item-number" @click="show()">
                        共5个活动 <i class="fas fa-caret-down"></i>
                    </div>
                    <div class="bottom-item-item">
                        <span class="green red">首</span><span>啊施工啊十分罕见</span>
                    </div>
                    <div class="bottom-item-item">
                        <span class="red">折</span><span>啊施工啊十分罕见</span>
                    </div>
                    <div class="bottom-item-item">
                        <span class="red">减</span><span>啊施工啊十分罕见</span>
                    </div>
                    <div class="bottom-item-item">
                        <span class="red">特</span><span>啊施工啊十分罕见</span>
                    </div>
                    <div class="bottom-item-item">
                        <span class="red">折</span><span>啊施工啊十分罕见</span>
                    </div>
                    <div class="bottom-item-item">
                        <span class="red">减</span><span>啊施工啊十分罕见</span>
                    </div>
                    <div class="bottom-item-item">
                        <span class="red">特</span><span>啊施工啊十分罕见</span>
                    </div>
            </div>
          </div>
      </header>
      <div class="main" ref="main" v-bind:class="{ 'main-fixed': isfixed}">
        <div class="main-select">
            <div class="main-select-button" v-bind:class="{ 'selected': main1}" @click="selected1()"><span>点餐</span></div>
            <div class="main-select-button" v-bind:class="{ 'selected': main2}" @click="selected2()"><span>评价</span></div>
            <div class="main-select-button" v-bind:class="{ 'selected': main3}" @click="selected3()"><span>商家</span></div>
        </div>
        <div class="main-show">
            <div class="shoplist" v-if="main1">
                <div class="shoplist-class">
                    <a :href="'#'+n.id" v-for="(n,index) in shopList" :key="index">
                        <span>{{n.title}}</span>
                        <span>{{n.list.length}}</span>
                    </a>
                </div>
                <div class="shoplist-item" v-bind:class="{ 'shoplist-item-scroll': isScroll}"
                 @mousewheel="mouse()"  ref="mouse">
                    <div v-for="(n,index) in shopList" :key="index" class="shoplist-item-wrap">
                        <a class="class-title" :id="n.id">{{n.title}}</a>
                        <div v-for="(item,index) in n.list" :key="index" class="list-item">
                            <img :src="item.pic" alt="">
                            <div class="list-item-wrap">
                                <h3>{{item.title}}</h3>
                                <p>月售{{item.number}}份 好评率{{item.good}}%</p>
                                <p>{{item.price}}</p>
                                <div class="buy-number">
                                    <button type="button" name="button" @click="add(item)">+</button>
                                    <div class="num" id="numChange">{{item.buynum}}</div>
                                    <button type="button" name="button" @click="red(item)">-</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div v-if="main2">222</div>
            <div v-if="main3">3333</div>
        </div>
      </div>
      <footer>
          <div class="car-wrap">
              <div class="car">
                  <i class="fas fa-cart-plus"></i>
                  <span class="carnum" v-if="cartNumber>0">{{cartNumber}}</span>
              </div>
          </div>
          <div class="total" v-if="cartNumber>0">{{cartPrice}}</div>
          <div class="sub" @click="setOrder">生成订单</div>         
      </footer>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { mapGetters } from 'vuex'
export default {
  components: {
  },
  data: function () {
    return {
      selected:1,
      isShow:false,
      isfixed:false,
      isScroll:false,
      main1:true,
      main2:false,
      main3:false
    }
  },
  computed: {
    ...mapState([
      'shopList',
      'order',
      'logined'
    ]),
    ...mapGetters([
        'cartList',
        'cartNumber',
        'cartPrice',
        'cartTotal'
    ])
  },
  created(){
    this.$store.dispatch('getShop');
    this.$store.commit('INIT_CART');
  },
  mounted() {
    window.onscroll = scroll =>this.height();
  },
  methods: {
      //加购物车
    add(data){
        data.buynum++;
        this.$store.commit('SET_CART', data);
    },
    red(data){
        if (data.buynum>0) {
            data.buynum--;
            this.$store.commit('SET_CART_OUT', data);
        }
    },
    //生成订单
    setOrder(){
        if (this.logined ){
            if ( this.cartNumber) {
            this.$store.commit('SET_ORDER',this.cartTotal); 
            this.$store.commit('INIT_CART'); 
            this.$router.push('/order'); 
            } else (
                alert ('请选择商品！')
            )  
        } else {
            alert ('请登陆');
            this.$router.push('/login'); 
        }
 
    },
    goback: function () {
      window.history.length > 1
        ? this.$router.go(-1)
        : this.$router.push('/')
    },
    show () {
        this.isShow = !this.isShow;
    },
    height () {
        let h;
        let element=this.$refs.main;
        if (element){
            if(element['offsetTop']-window.scrollY<=0){
                this.isfixed = true;
                this.isScroll = true;
            } else{
                this.isfixed = false; 
                this.isScroll = false;          
            } 
        }   
    },
    mouse () {       
        let element=this.$refs.mouse;
        let mouse=element['scrollTop'];  
        let e =window.event;  
        if (e.wheelDelta) {  //判断浏览器IE，谷歌滑轮事件               
            if (mouse==0 && e.wheelDelta > 0) { //当滑轮向上滚动时  
            this.isfixed = false;
            this.isScroll = false; 
            }  
        } else if (e.detail) {  //Firefox滑轮事件  
            if (mouse==0 && e.detail> 0) { //当滑轮向上滚动时  
            this.isfixed = false;
            this.isScroll = false; 
            }
        }  
    },
    selected1() {
        this.main1 = true;
        this.main2 = false;
        this.main3 = false;
    },
    selected2() {
        this.main1 = false;
        this.main2 = true;
        this.main3 = false;
    },
    selected3() {
        this.main1 = false;
        this.main2 = false;
        this.main3 = true;
    }   
  }
}
</script>

<style scoped>
/**/
    .shop{
        background-color: #ededed;
        width: 100vw;
        padding-bottom: 50vw;
    }
    header {
        width: 100%;
        position: relative;
        background-color: white;
    }
    nav{
        width: 100%;
        height: 20vw;
        background-color: black;
    }
    nav a{
        display: block;
        width: 15vw;
        height: 20vw;
        color: white;
        text-align: center;
        line-height: 20vw;
        font-size: 5vw;
    }
    .header-wrap{
        position: relative;
        width: 100vw;
        padding: 10vw 2.5vw  2vw 2.5vw;
        text-align: center;
    }
    .header-wrap img{
        position: absolute;
        top: -10vw;
        left: 50%;
        margin-left: -9vw;
        width: 18vw;
        height: 18vw;
        background-color: aquamarine;
    }
    .header-wrap h2{
        font-size: 5vw;
    }
    .header-wrap div{
        font-size: 3.2vw;
    }
    .header-wrap p{
        font-size: 3.2vw;
        color: #999;
    }
    .bottom-item-bottom{
        position: relative;
        width: 76vw;
        height: 7vw;
        overflow: hidden;
        margin: 2vw 12vw;
        text-align: left;
        padding-left: 2vw;
        border: 1px solid rgb(221, 219, 219);
    }
    .bottom-item-bottom-click{
        height: auto;
    }
    .item-number{
        position:absolute;
        right: 4vw;
        top: 1vw;
        height: 5vw;
        font-size: 3.2vw;
        line-height: 5vw;
    }
    .bottom-item-item{
        width: 100%;
        height: 5vw;
        font-size: 3.2vw;
        line-height: 5vw;
        margin: 1vw 0;
    }
    .bottom-item-item:first-child{
        margin-top: 0;
    }
    .bottom-item-item .red{
        display: inline-block;
        width: 4vw;
        font-size: 3vw;
        margin-right: 2vw;
        text-align: center;
        color: white;
        border-radius: 1vw;
        background-color: rgb(240, 115, 115);
    }
    .bottom-item-item .green{
        background-color: rgb(112, 184, 98);
    }
    .bottom-item-item span{
        display: inline-block;
    }
    .main{
        width: 100vw;
        background-color: white;
    }
    .main-fixed{
        position: fixed;
        top: 0;
    }
    .main-select{
        width: 100vw;
        height: 10vw;
        padding: 0 0.5vw;
        border-bottom: 1px solid rgb(219, 219, 219);
    }
    .main-select-button{
        position: relative;
        float: left;
        width: 33vw;
        height: 100%;
        text-align: center;
        font-size: 4vw;
        line-height: 10vw;
    }
    .selected{
        font-weight: 600;
    }
    .selected span::after{
        content: "";
        position: absolute;
        bottom: 0vw;
        left: 12.5vw;
        width: 8vw;
        height: .533333vw;
        background-color: #2395ff;
    }
/**/
/*shoplist*/
    .shoplist{
        position: relative;
        width: 100vw;
        height: 100vh;
    }
    .shoplist-class{
        padding-left: 2vw;
        float: left;
        width: 24vw;
        height: 100%;
        background-color: #f8f8f8;
    }
    .shoplist-class a{
        display: block;
        width: 100%;
        height: 12vw;
        color: #999;
        font-size: 3.6vw;
        line-height: 12vw;
    } 
    .shoplist-item{
        float: left;
        width: 76vw;
        height: 100%;
        overflow:hidden;
        padding-bottom: 25vw;
    }
    .shoplist-item-scroll{
        overflow:scroll;
    }
    .shoplist-item-wrap{
        width: 76vw;
    }
    .class-title{
        display: block;
        padding-left: 2vw;
        width: 76vw;
        height: 8vw;
        font-size: 4vw;
        line-height: 8vw;
        color: black;
    }
    .list-item{
        width: 76vw;
        height: 30vw;
        padding: 2vw;

    }
    .list-item img{
        height: 90%;
    }
    .list-item-wrap{
        position: relative;
        width: 48vw;
        height: 100%;
        float: right;
    }
    .list-item-wrap h3{
        font-size: 4vw;
        line-height: 6vw;
        color: black;
    }
    .list-item-wrap p{
        font-size: 3vw;
        line-height: 5vw;
    }
    .list-item-wrap p:last-of-type{
        position: absolute;
        bottom: 0;
        font-size: 3vw;
        line-height: 5vw;
        margin-bottom: 2vw;
        color: rgb(252, 80, 80);
        font-size: 4vw;
        font-weight: 600;
    }
    .list-item-wrap p:last-of-type::before{
        font-weight: 400;
        content: "\A5";
        font-size: 3vw;
        margin-right: .533333vw;
        display: inline-block;
    }
    .buy-number{
        position: absolute;
        right: 0;
        bottom: 2vw;
        width: 50%;
        height: 8vw;
    }
    .buy-number button{
        width: 30%;
        height: 8vw;
        float: right;
        background-color: inherit;
        border: 1px solid #e9ecef;
        outline:none;
        color: #464a4e;
    }
    .buy-number .num{
        width: 40%;
        height: 8vw;
        float: right;
        border-top: 1px solid #e9ecef;
        border-bottom: 1px solid #e9ecef;
        color: #464a4e;
        text-align: center;
        line-height: 8vw;
    }
/*shoplist*/
/*footer*/
  footer{
      position:fixed;
      bottom: 0;
      left: 0;
      width: 100vw;
      height: 14vw;
      background-color: rgb(88, 88, 88);
      color: #999;
  }
  .car-wrap{
      position: relative;
      float: left;
      width: 30vw;
      height: 100%;
  }
  .car{
      color: rgb(255, 255, 255);
      background-color: rgb(92, 134, 252);
      height: 14vw;
      width: 14vw;
      border: 2vw solid rgb(75, 74, 74);
      border-radius: 7vw;
      text-align: center;
      line-height: 10vw;
      font-size: 5vw;
      position: relative;
      top: -3vw;
      left: 4vw;
  }
  .carnum{
      display: block;
      width: 5vw;
      height: 5vw;
      font-size: 3vw;
      line-height: 5vw;
      position: absolute;
      bottom: -3vw;
      right: -3vw;
      color: #e03c3c;
      border-radius: 2.5vw;
      background-color: white;
  }
  .total{
      width: 14vw;
      height: 100%;
      position: absolute;
      top: 0;
      left: 30vw;
      font-size: 4vw;
      color: white;
      line-height: 14vw;
  }
  .total::before{
    font-weight: 500;
    content: "\A5";
    font-size: 5vw;
    margin-right: .533333vw;
    display: inline-block;
  }
  .sub{
      width: 30vw;
      height: 100%;
      position: absolute;
      top: 0;
      right: 0;
      font-size: 4vw;
      text-align: center;
      color: white;
      background-color: rgb(91, 114, 218);
      line-height: 14vw;     
  }
/*footer*/
</style>



